<template>
  <div class="nav">
      <nav class="n-box">
          <!-- logo -->
          <router-link tag="a" to="/" class="icon">
            <img src="../../common/image/logo.svg" alt="logo">
          </router-link>
          <!-- 导航 -->
          <ul class="n-list">
              <!-- 主导航 -->
              <li class="n-lin">
                  <div class="n-phone">
                      <span>首页</span>
                  </div>
                  <ul class="n-ull">
                       <router-link tag="li" class="mine" to="/"><a href="" class="active">首页</a></router-link>
                       <router-link tag="li" class="mine" to="/book"><a href="">小册</a></router-link>
                       <router-link tag="li" class="mine" to="/repos"><a href="">开源库</a></router-link>
                       <router-link tag="li" class="mine" to="/activ"><a href="">活动</a></router-link>
                  </ul>
              </li>
              <!-- 搜索 -->
              <li class="n-f">
                  <form class="n-form">
                      <input type="text" placeholder='搜索掘金' class="n-input">
                      <img class="v-so" src="data:image/svg+xml;base64," alt="">
                  </form>
              </li>
              <!-- 写文章 -->
              <li class="n-group">
                  <div class="n-gd">
                       <button disabled="disabled" class="n-btn">写文章</button>
                       <div class="n-more"  @click="show()" ><i>.</i></div>
                        <ul class="n-ml" v-show="sh">
                            <li>分享链接</li>
                        </ul>
                  </div>
              </li>
            <!-- 提醒 -->
              <li class="n-item">
                  <a href="" class="n-li"></a>
              </li>
              <!-- 头像 -->
              <li class="n-menu">
                  <div class="n-img"></div>
              </li>
              <li class="n-d">
                  <ul>
                      <li>|</li>
                      <li>登录</li>
                      <li class="n-l">.</li>
                      <li>注册</li>
                  </ul>
              </li>
          </ul>
      </nav>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data () {
    return {
      sh: false
    }
  },
  methods: {
    show () {
      if (this.sh === false) {
        this.sh = true
      } else {
        this.sh = false
      }
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/base.styl";
.nav
    width 100%
    height 60px
    background $color
    border-bottom 1px solid #F1F1F1
    .n-box
        display flex
        box-sizing border-box
        align-items center
        margin 0 auto
        width 960px
        .icon
            flex 0
            padding-right 45px
        .icon img
            flex 0 0 auto
            display inline-block
        & .n-list
            flex 1
            display flex
            height 60px
            // 导航
            .n-lin
                flex 2
                line-height  60px
                height 100%
                .n-phone
                    display none
                .n-ull
                    display flex
                    height 100%
                    .mine
                        display inline-block
                        width 70px
                        height 100%
                        a
                            display inline-block
                            width 100%
                        .active
                            color $hcolor
            // 搜索
            .n-f
                flex 1.1
                width 10px
                margin-left -85px
                .n-form
                    border 1px solid #E5E6E7
                    height 34px
                    background #F9FAFB
                    display flex
                    align-items center
                    justify-content space-between
                    border-radius 2px
                    margin 10px
                    .n-input
                        border none
                        padding 8px
                        font-size 14px
                        outline none
                        box-shadow none
                        background #F9FAFB
                    .v-so
                        cursor pointer
                        margin-left  -25px
                //  写文章
            .n-group
                flex 0.3
                display flex
                left 20pxs
                padding-left 30px
                padding-top 13px
                .n-gd
                    display flex
                    align-items center
                    height 30px
                    background #006CFF
                    border-radius 5px
                    position relative
                    .n-btn
                        border none
                        background #006CFF
                        border-top-left-radius 5px
                        border-bottom-left-radius 5px
                        height 100%
                        color $color
                        border-right  1px solid #fff
                        cursor pointer
                    .n-btn:hover
                         background #317ade
                    .n-more
                        width 10px
                        height 100%
                        cursor pointer
                        i
                            color #fff
                    .n-more:hover
                          background #317ade
                    .n-ml
                        position absolute
                        top 100%
                        left 0
                        right 0
                        padding 5 0
                        font-size 16px
                        color #3b76c5
                        background-color $color
                        border 1px solid #ebebeb
                        border-radius 2px
                        box-shadow 0 1px 2px 0 rgba(0,0,0,.05)
                        z-index 1
                        font-size 14px
                        line-height 30px
                // 铃铛
            .n-item
                width 30px
                a
                    background url('../../common/image/lingdang2.svg') no-repeat
                    background-size 20px
                    display inline-block
                    width 20px
                    height 20px
                    margin-top: 20px
                    margin-left 5px
                a:hover
                    background url('../../common/image/lingdang.svg') no-repeat
                    background-size 20px
            // 头像
            .n-menu
                width 30px
                height 100%
                .n-img
                   border-radius 15px
                   width 30px
                   height 30px
                   background #ccc
                   margin-top 12px
                   margin-left 5px
            .n-d
                position relative
                display none
                ul
                    position absolute
                    top: 20px
                    left -60px
                    display flex
                    li
                        flex 1
                        width 35px
                        color $hcolor
</style>



